import {
  DataSourceInstanceSettings,
  CoreApp,
  ScopedVars,
  MetricFindValue,
  LegacyMetricFindQueryOptions,
  DataQueryRequest,
  DataQueryResponse,
  DataFrame,
} from '@grafana/data';
import { DataSourceWithBackend, getTemplateSrv, getBackendSrv } from '@grafana/runtime';
import { Observable, merge } from 'rxjs';
import { tap } from 'rxjs/operators';

import { DatadogQuery, DatadogDataSourceOptions, DEFAULT_QUERY } from './types';

export class DataSource extends DataSourceWithBackend<DatadogQuery, DatadogDataSourceOptions> {
  constructor(instanceSettings: DataSourceInstanceSettings<DatadogDataSourceOptions>) {
    super(instanceSettings);
  }

  annotations = {};
  
  logs = {
    startPages: 1,
    limit: 1000,
    supportedModes: ['streaming', 'tail'],
  };

  async getMetrics(): Promise<string[]> {
    try {
      const response = await getBackendSrv().get(`/api/datasources/${this.id}/resources/metrics`);
      
      if (response.error) {
        throw new Error(response.error);
      }
      
      if (!response.metrics || !Array.isArray(response.metrics)) {
        return [];
      }
      
      return response.metrics;
    } catch (err) {
      throw err;
    }
  }

  async getTags(metric?: string): Promise<string[]> {
    try {
      if (!metric) {
        return ['*'];
      }

      const params = `?metric=${encodeURIComponent(metric)}`;
      const response = await getBackendSrv().get(`/api/datasources/${this.id}/resources/tags${params}`);
      
      if (response.error) {
        throw new Error(response.error);
      }
      
      const variables = getTemplateSrv().getVariables();
      const variableOptions = variables.map(variable => `$${variable.name}`);
      
      const datadogTags = response.tags || [];
      const allOptions = [...new Set(['*', ...variableOptions, ...datadogTags])];
      
      return allOptions;
    } catch (err) {
      return ['*'];
    }
  }

  getDefaultQuery(_: CoreApp): Partial<DatadogQuery> {
    return DEFAULT_QUERY;
  }

  applyTemplateVariables(query: DatadogQuery, scopedVars: ScopedVars) {
    const result = {
      ...query,
    };

    if (query.queryType === 'metrics') {
      result.metric = getTemplateSrv().replace(query.metric || '', scopedVars);
      result.from = getTemplateSrv().replace(query.from || '', scopedVars);
      result.alias = getTemplateSrv().replace(query.alias || '', scopedVars);
      
      if (query.groupBy) {
        result.groupBy = query.groupBy.map(group => 
          getTemplateSrv().replace(group, scopedVars)
        );
      }
    }
    
    if (query.queryType === 'logs') {
      result.searchQuery = getTemplateSrv().replace(query.searchQuery || '', scopedVars);
    }

    return result;
  }

  filterQuery(query: DatadogQuery): boolean {
    return !!query.metric;
  }

  async metricFindQuery(query: string, options?: LegacyMetricFindQueryOptions): Promise<MetricFindValue[]> {
    try {
      const params = query ? `?metric=${encodeURIComponent(query)}` : '';
      const response = await getBackendSrv().get(`/api/datasources/${this.id}/resources/tags${params}`);
      
      if (response.error) {
        throw new Error(response.error);
      }
      
      const variables = getTemplateSrv().getVariables();
      const variableOptions = variables.map(variable => `$${variable.name}`);
      
      const datadogTags = response.tags || [];
      const allOptions = [...new Set(['*', ...variableOptions, ...datadogTags])];
      
      let filteredTags = allOptions;
      if (query && !query.includes(':')) {
        if (query.startsWith('/') && query.endsWith('/')) {
          try {
            const regex = new RegExp(query.slice(1, -1));
            filteredTags = allOptions.filter(tag => regex.test(tag));
          } catch (e) {
            console.error('Invalid regex:', e);
          }
        } else {
          filteredTags = allOptions.filter(tag => 
            tag.toLowerCase().includes(query.toLowerCase())
          );
        }
      }

      return filteredTags.map(tag => ({
        text: tag,
        value: tag,
      }));
    } catch (err) {
      console.error('Error in metricFindQuery:', err);
      return [{ text: '*', value: '*' }];
    }
  }

  query(request: DataQueryRequest<DatadogQuery>): Observable<DataQueryResponse> {
    const metricsQueries = request.targets.filter(q => q.queryType === 'metrics');
    const logsQueries = request.targets.filter(q => q.queryType === 'logs');

    const observables: Observable<DataQueryResponse>[] = [];

    if (metricsQueries.length > 0) {
      const metricsRequest: DataQueryRequest<DatadogQuery> = {
        ...request,
        targets: metricsQueries,
      };
      observables.push(this.processMetricsQuery(metricsRequest));
    }

    if (logsQueries.length > 0) {
      const logsRequest: DataQueryRequest<DatadogQuery> = {
        ...request,
        targets: logsQueries,
      };
      observables.push(this.processLogsQuery(logsRequest));
    }

    if (observables.length === 0) {
      return super.query(request);
    }

    return merge(...observables);
  }

  private processMetricsQuery(request: DataQueryRequest<DatadogQuery>): Observable<DataQueryResponse> {
    return super.query(request);
  }

  private processLogsQuery(request: DataQueryRequest<DatadogQuery>): Observable<DataQueryResponse> {
    return super.query(request).pipe(
      tap((response: DataQueryResponse) => {
        if (response.data) {
          response.data = response.data.map((frame: DataFrame) => {
            frame.meta = {
              ...frame.meta,
              preferredVisualisationType: 'logs',
              custom: {
                ...(frame.meta?.custom || {}),
                logsResult: true,
                searchWords: request.targets
                  .map(q => q.searchQuery)
                  .filter(q => q),
              },
            };
            return frame;
          });
        }
      })
    );
  }
}
