import React, { useEffect, useState } from 'react';
import { InlineField, Input, Select, Stack, Button, TabsBar, Tab } from '@grafana/ui';
import { QueryEditorProps, SelectableValue } from '@grafana/data';
import { DataSource } from '../datasource';
import { DatadogDataSourceOptions, DatadogQuery } from '../types';

type Props = QueryEditorProps<DataSource, DatadogQuery, DatadogDataSourceOptions>;

const AGGREGATION_OPTIONS = [
  { label: 'Average', value: 'avg' },
  { label: 'Sum', value: 'sum' },
  { label: 'Min', value: 'min' },
  { label: 'Max', value: 'max' },
];

const QUERY_TYPES: Array<{ label: string; value: 'metrics' | 'logs' }> = [
  { label: 'Metrics', value: 'metrics' },
  { label: 'Logs', value: 'logs' },
];

const QUERY_MODES: Array<{ label: string; value: 'search' | 'aggregate' }> = [
  { label: 'Search', value: 'search' },
  { label: 'Aggregate', value: 'aggregate' },
];

const SORT_OPTIONS: Array<{ label: string; value: 'timestamp_desc' | 'timestamp_asc' }> = [
  { label: 'Timestamp DSC', value: 'timestamp_desc' },
  { label: 'Timestamp ASC', value: 'timestamp_asc' },
];

export function QueryEditor({ query, onChange, onRunQuery, datasource }: Props) {
  const [metrics, setMetrics] = useState<Array<SelectableValue<string>>>([]);
  const [tags, setTags] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingTags, setIsLoadingTags] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function loadMetrics() {
      setIsLoading(true);
      setError(null);
      try {
        const metricsList = await datasource.getMetrics();
        setMetrics(
          metricsList.map((m) => ({
            label: m,
            value: m,
          }))
        );
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load metrics');
      } finally {
        setIsLoading(false);
      }
    }
    loadMetrics();
  }, [datasource]);

  useEffect(() => {
    async function loadTags() {
      if (!query.metric) {
        setTags(['*']);
        return;
      }

      setIsLoadingTags(true);
      try {
        const tagsList = await datasource.getTags(query.metric);
        if (tagsList && tagsList.length > 0) {
          setTags(tagsList);
        } else {
          setTags(['*']);
        }
      } catch (err) {
        console.error('Failed to load tags:', err);
        setTags(['*']);
      } finally {
        setIsLoadingTags(false);
      }
    }
    loadTags();
  }, [query.metric, datasource]);

  const clearUnusedFields = (queryType: 'metrics' | 'logs') => {
    if (queryType === 'logs') {
      return {
        queryType,
        searchQuery: query.searchQuery || '',
        queryMode: query.queryMode || 'search',
        sort: query.sort || 'timestamp_desc',
        limit: query.limit || 1000,
        format: 'logs' as const,
      };
    } else {
      return {
        queryType,
        metric: query.metric || '',
        aggregation: query.aggregation || 'avg',
        from: query.from || '*',
        groupBy: query.groupBy || [],
        alias: query.alias || '',
        format: 'time_series' as const,
      };
    }
  };

  useEffect(() => {
    if (!query.queryType) {
      onChange({
        ...query,
        ...clearUnusedFields('metrics'),
      });
    }
  }, [query, onChange]);

  const onMetricChange = (value: SelectableValue<string>) => {
    const newMetric = value?.value || '';
    onChange({ ...query, metric: newMetric, from: '*' });
    onRunQuery();
  };

  const onFromChange = (value: SelectableValue<string> | null) => {
    onChange({ ...query, from: value?.value || '*' });
    onRunQuery();
  };

  const onAggregationChange = (value: SelectableValue<string>) => {
    onChange({ ...query, aggregation: value.value || 'avg' });
    onRunQuery();
  };

  const onAddGroupBy = () => {
    const currentGroups = query.groupBy || [];
    onChange({
      ...query,
      groupBy: [...currentGroups, ''],
    });
  };

  const onGroupByChange = (index: number, value: string) => {
    const currentGroups = [...(query.groupBy || [])];
    currentGroups[index] = value;
    onChange({ ...query, groupBy: currentGroups });
    onRunQuery();
  };

  const onRemoveGroupBy = (index: number) => {
    const currentGroups = [...(query.groupBy || [])];
    currentGroups.splice(index, 1);
    onChange({ ...query, groupBy: currentGroups });
    onRunQuery();
  };

  const onAliasChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange({ ...query, alias: event.target.value });
    onRunQuery();
  };

  const onSearchQueryChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newQuery = event.target.value;
    onChange({ 
      ...query, 
      searchQuery: newQuery,
      queryMode: query.queryMode || 'search',
      sort: query.sort || 'timestamp_desc',
      limit: query.limit || 1000
    });
    setTimeout(onRunQuery, 500);
  };

  const onQueryTypeChange = (value: 'metrics' | 'logs') => {
    onChange({
      ...query,
      ...clearUnusedFields(value),
    });
    onRunQuery();
  };

  const onQueryModeChange = (value: SelectableValue<'search' | 'aggregate'>) => {
    onChange({ ...query, queryMode: value.value });
    onRunQuery();
  };

  const onSortChange = (value: SelectableValue<'timestamp_desc' | 'timestamp_asc'>) => {
    onChange({ ...query, sort: value.value });
    onRunQuery();
  };

  const onLimitChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const limit = parseInt(event.target.value, 10);
    onChange({ ...query, limit: isNaN(limit) ? 1000 : limit });
    onRunQuery();
  };

  const renderMetricsEditor = () => (
    <>
      <Stack direction="row" gap={1}>
        <InlineField label="Metric" labelWidth={8}>
          <Select
            options={metrics}
            value={query.metric ? { label: query.metric, value: query.metric } : null}
            onChange={onMetricChange}
            isLoading={isLoading}
            placeholder={error || "Select metric"}
            width={32}
            allowCustomValue
            invalid={!!error}
          />
        </InlineField>
        <InlineField label="From" labelWidth={8}>
          <Select<string>
            options={tags.map(tag => ({
              label: tag,
              value: tag,
            }))}
            value={query.from ? { label: query.from, value: query.from } : null}
            onChange={onFromChange}
            isLoading={isLoadingTags}
            placeholder="$host"
            width={20}
            allowCustomValue
            isClearable
            noOptionsMessage="No tags available"
          />
        </InlineField>
      </Stack>
      <Stack direction="row" gap={1}>
        <InlineField label="Aggregate" labelWidth={8}>
          <Select
            options={AGGREGATION_OPTIONS}
            value={query.aggregation}
            onChange={onAggregationChange}
            width={20}
          />
        </InlineField>
        <Button
          variant="secondary"
          icon="plus"
          onClick={onAddGroupBy}
          size="md"
        >
          Group By
        </Button>
      </Stack>
      {(query.groupBy || []).map((group, index) => (
        <Stack direction="row" gap={1} key={index}>
          <InlineField label={index === 0 ? 'Group By' : ''} labelWidth={8}>
            <Select<string>
              options={tags.map(tag => ({
                label: tag,
                value: tag,
              }))}
              value={group}
              onChange={(v) => onGroupByChange(index, v.value || '')}
              isLoading={isLoadingTags}
              placeholder="Select tag"
              width={32}
              allowCustomValue
            />
          </InlineField>
          <Button
            variant="secondary"
            icon="minus"
            onClick={() => onRemoveGroupBy(index)}
            size="md"
          />
        </Stack>
      ))}
      <Stack direction="row" gap={1}>
        <InlineField 
          label="Alias" 
          labelWidth={8}
          tooltip="Use variables: $metric, $aggregation, $from, $tag_tagname"
        >
          <Input
            onChange={onAliasChange}
            value={query.alias || ''}
            placeholder="$metric on $from"
            width={84}
          />
        </InlineField>
      </Stack>
    </>
  );

  const renderLogsEditor = () => (
    <>
      <Stack direction="row" gap={1}>
        <InlineField label="Search" labelWidth={8} tooltip="Datadog search syntax">
          <Input
            value={query.searchQuery || ''}
            onChange={onSearchQueryChange}
            placeholder="service:my-service status:error"
            width={60}
            autoFocus={!query.searchQuery}
          />
        </InlineField>
      </Stack>
      <Stack direction="row" gap={1}>
        <InlineField label="Mode" labelWidth={8}>
          <Select
            options={QUERY_MODES}
            value={query.queryMode || 'search'}
            onChange={onQueryModeChange}
            width={20}
          />
        </InlineField>
      </Stack>
      <Stack direction="row" gap={1}>
        <InlineField label="Sort by" labelWidth={8}>
          <Select
            options={SORT_OPTIONS}
            value={query.sort || 'timestamp_desc'}
            onChange={onSortChange}
            width={20}
          />
        </InlineField>
        <InlineField label="Limit" labelWidth={8} tooltip="Maximum number of logs">
          <Input
            type="number"
            value={query.limit || 1000}
            onChange={onLimitChange}
            placeholder="1000"
            width={20}
          />
        </InlineField>
      </Stack>
    </>
  );

  return (
    <Stack direction="column" gap={0.5}>
      <TabsBar>
        {QUERY_TYPES.map(({ label, value }) => (
          <Tab
            key={value}
            label={label}
            active={query.queryType === value}
            onChangeTab={() => onQueryTypeChange(value)}
          />
        ))}
      </TabsBar>

      {query.queryType === 'metrics' ? renderMetricsEditor() : renderLogsEditor()}
    </Stack>
  );
}
