import { DataSourceJsonData } from '@grafana/data';
import { DataQuery } from '@grafana/schema';

export type QueryType = 'metrics' | 'logs';

export interface DatadogQuery extends DataQuery {
  queryType: QueryType;
  metric?: string;
  aggregation?: string;
  from?: string;
  groupBy?: string[];
  alias?: string;
  queryMode?: 'search' | 'aggregate';
  searchQuery?: string;
  sort?: 'timestamp_desc' | 'timestamp_asc';
  limit?: number;
  format?: 'time_series' | 'logs';
}

export const DEFAULT_QUERY: Partial<DatadogQuery> = {
  queryType: 'metrics',
  format: 'time_series',
  aggregation: 'avg',
  alias: '',
  limit: 1000,
  sort: 'timestamp_desc',
  queryMode: 'search',
};

/**
 * These are options configured for each DataSource instance
 */
export interface DatadogDataSourceOptions extends DataSourceJsonData {
  apiUrl?: string;
}

/**
 * Value that is used in the backend, but never sent over HTTP to the frontend
 */
export interface DatadogSecureJsonData {
  apiKey?: string;
  appKey?: string;
}
