import React, { ChangeEvent } from 'react';
import { InlineField, SecretInput } from '@grafana/ui';
import { DataSourcePluginOptionsEditorProps } from '@grafana/data';
import { DatadogDataSourceOptions, DatadogSecureJsonData } from '../types';

interface Props extends DataSourcePluginOptionsEditorProps<DatadogDataSourceOptions, DatadogSecureJsonData> {}

export function ConfigEditor(props: Props) {
  const { onOptionsChange, options } = props;
  const { secureJsonFields, secureJsonData } = options;

  const onAPIKeyChange = (event: ChangeEvent<HTMLInputElement>) => {
    onOptionsChange({
      ...options,
      secureJsonData: {
        ...secureJsonData,
        apiKey: event.target.value,
      },
    });
  };

  const onAppKeyChange = (event: ChangeEvent<HTMLInputElement>) => {
    onOptionsChange({
      ...options,
      secureJsonData: {
        ...secureJsonData,
        appKey: event.target.value,
      },
    });
  };

  const onResetAPIKey = () => {
    onOptionsChange({
      ...options,
      secureJsonFields: {
        ...options.secureJsonFields,
        apiKey: false,
      },
      secureJsonData: {
        ...options.secureJsonData,
        apiKey: '',
      },
    });
  };

  const onResetAppKey = () => {
    onOptionsChange({
      ...options,
      secureJsonFields: {
        ...options.secureJsonFields,
        appKey: false,
      },
      secureJsonData: {
        ...options.secureJsonData,
        appKey: '',
      },
    });
  };

  return (
    <>
      <InlineField label="API Key" labelWidth={14} tooltip="Your Datadog API key">
        <SecretInput
          isConfigured={secureJsonFields.apiKey}
          value={secureJsonData?.apiKey || ''}
          placeholder="Enter your Datadog API key"
          width={40}
          onReset={onResetAPIKey}
          onChange={onAPIKeyChange}
        />
      </InlineField>
      <InlineField label="Application Key" labelWidth={14} tooltip="Your Datadog Application key">
        <SecretInput
          isConfigured={secureJsonFields.appKey}
          value={secureJsonData?.appKey || ''}
          placeholder="Enter your Datadog Application key"
          width={40}
          onReset={onResetAppKey}
          onChange={onAppKeyChange}
        />
      </InlineField>
    </>
  );
}
